:root {
  --color-white: #fff;
  --color-black: #000;
  --color-dark-gray: #10100e;
  --color-yellow: #ffff00;
  --color-blue: #6a83fb;
  --color-violet: #ae72c2;
  --color-red: #f75d83;

  --font-family-main: sans-serif;
  --font-size-header: 26px;
  --font-size-sub-header: 18px;
  --font-size-main: 16px;
  --font-bold: 600;

  --width-max: 1200px;

  --text-shadow-yellow:
    0 0 2px var(--color-white),
    0 0 40px var(--color-yellow);
  --text-shadow-blue:
    0 0 2px var(--color-white),
    0 0 40px var(--color-blue);

  --box-shadow-white:
    0 16px 24px 2px rgba(255, 255, 255, 0.02), 
    0 6px 30px 5px rgba(255, 255, 255, 0.04), 
    0 8px 10px -5px rgba(255, 255, 255, 0.1);
  --box-shadow-black:
    0 16px 24px 2px rgba(0, 0, 0, 0.5), 
    0 6px 30px 5px rgba(0, 0, 0, 0.1), 
    0 8px 10px -5px rgba(0, 0, 0, 0.2);
    
  --spacing-very-small: 5px;
  --spacing-small: 10px;
  --spacing-medium: 20px;
  --spacing-large: 30px;

  --margin-list: 7px;

  --border-radius-small: 3px;
  --border-radius-medium: 5px;

  --transition: 0.4s;


  /* THEMES */

  --theme-light-header: #dfe2df;
  --theme-dark-header: #ca6c6f;
  --theme-neitral-header: #cccc46;
  --theme-default-header: var(--theme-neitral-header);

  --theme-light-bgImg: linear-gradient(to right bottom, #051937, #162454, #312d70, #533389, #7a359f);
  --theme-dark-bgImg:  linear-gradient(to right bottom, #000000, #160b11, #20141e, #281b2e, #2b2440);
  --theme-neitral-bgImg: url('@static/bg.jpg');
  --theme-default-bgImg: var(--theme-neitral-bgImg);
}

*, 
*:after, 
*:before {
    box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: var(--font-family-main);
  font-size: var(--font-size-main);
  background-color: var(--color-dark-gray);
  background-image: var(--theme-default-bgImg);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: top left;
  background-attachment: fixed;
}

h1,h2,h3,h4,h5,h6 {
  margin: 0;
  padding: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

button {
  padding: 0;
  border: none;
  font: inherit;
  color: inherit;
  background-color: transparent;
  cursor: pointer;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

.header__text {
  line-height: 1.7;
  font-size: var( --font-size-header);
  text-shadow: 0 0 2px var(--color-black);
  color: var(--theme-default-header);
  transition: color var(--transition)
}

/* SCROLL */

* {
	scrollbar-color: rgb(37, 56, 97) rgba(0, 0, 0, .7);
	scrollbar-width: thin;
}

::-webkit-scrollbar {
	width: 8px;
	height: 8px;
}
::-webkit-scrollbar-thumb {
	background-color: rgb(37, 56, 97);
	border-radius: 2px;
}
::-webkit-scrollbar-track {
	background-color: rgba(0, 0, 0, .7);
}
::-webkit-scrollbar-corner {
	background-color: rgba(0, 0, 0, .7);	
}